@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{margin:auto;box-sizing:border-box;font-family:"Quicksand"}.BookGroup{margin:auto;width:100%;background-color:#F8F8F8}.BookGroup .book-list{margin:auto;width:85%;background-color:#F8F8F8}.BookGroup .book-card{width:250px;height:100%;margin:30px 30px 0px 30px;display:inline-table;border:2px solid #F0F0F0}.BookGroup .book-card:hover{box-shadow:0 10px 30px rgba(0,0,0,0.3)}.BookGroup .book-card a{text-decoration:none;color:black}.desc{width:250px;height:100%;padding-left:10px}img{width:250px;height:250px;object-fit:contain;background-color:#E8E8E8}

.navbar{width:100%}.navbar-container{position:-webkit-sticky;position:sticky;top:0px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:100px;width:80%;margin:0 auto;border-bottom:1.5px solid #f2f2f2}@media screen and (max-width: 900px){.navbar-container{-webkit-flex-direction:column;flex-direction:column;padding:3% 0;height:150px}}@media screen and (max-width: 500px){.navbar-container{height:220px;padding:3% 0}}.navbar-icon{font-size:2rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;justify-self:start}@media screen and (max-width: 500px){.navbar-icon{-webkit-flex-direction:column;flex-direction:column;padding:3% 0;font-size:1.7rem}}@media screen and (max-width: 350px){.navbar-icon{padding:5% 0;font-size:1.4rem}}.navbar-form{width:auto}.navbar input{width:250px;padding:6px 10px;margin:10px 0px;border:1px solid #ddd;font-size:1rem}@media screen and (max-width: 530px){.navbar input{width:100%}}.navbar button{width:80px;padding:7px 0px;background-color:#F0F0F0;text-decoration:none;margin-right:10px}@media screen and (max-width: 530px){.navbar button{width:48%}}@media screen and (max-width: 350px){.navbar button{width:100%;margin-bottom:10px}}.navbar select{width:90px;padding:6px 5px;border:1px solid #ddd}@media screen and (max-width: 530px){.navbar select{width:48%}}@media screen and (max-width: 350px){.navbar select{width:100%}}.navbar .container{z-index:1;width:100%;max-width:1300px;margin-right:auto;margin-left:auto;padding-right:50px;padding-left:50px}

*{box-sizing:border-box;margin:0;padding:0;font-family:'PT Sans', sans-serif}

